/** @format */

.Signup {
  background-color: #f3f7f9;
  width: 100%;
  font-family: Roboto;
  text-align: center;
}
.Logo_Section {
  padding: 20px;
  width: 100%;
  text-align: center;
  background-color: #f3f7f9;
}

main.Form_Section {
  background-color: #fff;
  border-top-left-radius: 24px;
  border-top-right-radius: 24px;
  border-bottom-right-radius: 0;
  border-bottom-left-radius: 0;
  box-shadow: 0 -4px 5px 0 rgb(0 0 0 / 8%);
  height: 100%;
}

.Form_Header {
  padding-top: 32px;
  display: grid;
  grid-template-columns: 15% 80%;
}

.Form_Header .Back_Btn {
  align-self: center;
}

.Form_Header .Header_Text h1 {
  text-align: start;
  font-size: 24px;
  font-weight: 500;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.17;
  letter-spacing: normal;
  color: #3b414a;
}

main form div {
  position: relative;
  width: 80%;
  margin: 30px auto;
  display: grid;
  grid-template-columns: 1fr;
}

label {
  pointer-events: none;
}

main .Form form input:focus + label,
main .Form form input:valid + label,
main .Form form input:read-only + label {
  left: calc(20px - 1.5%);
  top: 25%;
  font-size: 10px;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: normal;
  letter-spacing: normal;
}

main .Form form button svg {
  margin-left: 10px;
}

label {
  transform: translate(10%, -50%);
  position: absolute;
  top: 50%;
  left: 5%;
  text-align: left;
  color: #7b8aa2;
  transition: 0.2s ease-in-out;
  pointer-events: none;
}

input {
  padding-top: 10px;
  padding-left: 20px;
  width: 100%;
  margin: auto;
  height: 52px;
  border-radius: 8px;
  background-color: #f2f3f7;
  border: none;
  outline: none;
  font-family: Roboto;
  font-size: 16px;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: normal;
  letter-spacing: normal;
  color: #4c5666;
}

input:focus + label,
input:valid + label,
input:read-only + label {
  left: calc(20px - 1.5%);
  top: 25%;
  font-size: 10px;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: normal;
  letter-spacing: normal;
}
